.homePageMainContent{
    width: 92%;
    max-width: 500px;
    height: 100vh;
    margin: auto;
    /* background-color: red; */
}
.homePageMainTop{
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
}
.homePageMainTitle{
    color: #22A5A3;
    font-family: "New Amsterdam", sans-serif;
    font-size: 320%;
    margin-top: 10px;
}
.homePageMainLeft{
    display: flex;
}
.homePageMainIconSpace{
    width: 35px;
    height: 35px;
    border-radius: 50px;
    display: flex;
    margin-top: 25px;
}
.homePageTopIcon{
    width: 30px;
    height: 30px;
    border-radius: 50px;
    margin: auto;
    object-fit: cover;
}
.homePageMainUserName{
    font-family: "Nunito", sans-serif;
    margin-top: 28px;
    font-weight: 700;
    font-size: 130%;
    margin-left: 8px; 
    color: #6f6f6f;   
}
.homePageMainBottom{
    width: 100%;
    /* height: calc(100vh - 80px); */
}
.homePageSettingTokenSpace{
    margin-top: 20px;
}
.homePageSettingTokenSpaceTitle{
    font-family: "Nunito", sans-serif;
    font-weight: 800;
    font-size: 150%;
    color: #6f6f6f;   
    display: flex;
    margin-left: 5px;
}
.homePageSettingTokenSpaceLine{
    margin: auto;
    width: 100%;
    height: 1px;
    background-color: #767676;
    display: flex;
    margin-top: 12px;
    border-radius: 50px;
}
.homePageSettingToken{
    width: 100%;
    margin-top: 5px;
    display: flex;
}
.homePageSettingTokenInputSpace{
    width: calc(100% - 35px);
    height: 35px;
    background-color: white;
    border: solid 2px #767676;
    border-radius: 50px;
    display: flex;
    margin-top: 10px;
}
.homePageSettingTokenInputCopyButton{
    width: 38px;
    height: 38px;
    margin-left: 10px;
    border-radius: 50px;
    border: none;
    background-color:rgb(34, 165, 163);
    border: solid 2px rgb(34, 165, 163);
    margin-top: 10px;
    display: flex;
    cursor: pointer;
}
.homePageSettingTokenInputCopyButton:hover{
    background-color:rgb(30, 136, 134);
}
.homePageSettingTokenInput{
    margin-left: 15px;
    width: calc(100% - 35px);
    height: 25px;
    margin: auto;
    border: none;
    outline: none;
    font-family: "Nunito", sans-serif;
    font-size: 120%;
    font-weight: 500;
    color: #767676;
}
.homePageMachineContents{
    width: 99%;
    margin: auto;
}
.homePageSettingTokenInputCopyButtonIcon{
    width: 20px;
    height: 20px;
    margin: auto;
}