.HostMachineMain{
    width: 100%;
    height: 70px;
    background-color: red;
    margin-top: 15px;
    border-radius: 15px;
    background-color: #FFFFFE;
    border: solid 2px rgb(34, 165, 163);
    display: flex;
}
.HostMachineMainTitle{
    font-family: "Nunito", sans-serif;
    color: rgb(0, 0, 0);
    font-weight: 700;
    display: flex;
    font-size: 140%;
    margin-top: 8px;
    margin-left: 5px;
}
.HostMachineMainIcon{
    width: 60px;
    margin-left: 5px;
}
.HostMachineMainButtons{
    display: flex;
    margin-left: 5px;
    height: 20px;
}
.HostMachineButton{
    border: none;
    font-family: "Nunito", sans-serif;
    background-color: #A79E94;
    border-radius: 50px;
    margin-right: 8px;
    cursor: pointer;
}
.connect{
    background-color: #40ACCB;
}
.restart{
    background-color: #DEBC5C;
}
.HostMachineDeleteButtonIcon{
    width: 18px;
    height:18px;
}
.HostMachineDeleteButton{
    width: 20px;
    height: 20px;
    border: none;
    background-color: rgba(0, 0, 0, 0);
    cursor: pointer;
}
.offButton{
    background-color: #A79E94 !important
}
.HostMachineMainButtonsInput{
    background-color: white;
    border: solid 2px #767676;
    border-radius: 50px;
    width: 75px;
    margin-right: 8px;
    font-family: "Nunito", sans-serif;
}